import {mapActions,mapState} from 'vuex'
export default{
	name:'Classify',
	data(){
		return{
			show:'',
			index:0
		}
	},
	methods:{
		handlelist(item){
			var name=JSON.stringify(item)
			localStorage.setItem('name',name)
			this.$router.push({path:'/footer/list'})
		},
		handleuu(idx){
			this.index=idx
		}
	},
	...mapActions({
			created:'fetchList'
		}),
	computed:{
		...mapState(['classify']),
		...mapState(["uu"])
	}
}



